import React from 'react';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  Line,
  Cell,
} from 'recharts';
import { MORankingData } from '../../types/dashboard.types';

interface MORankingProps {
  data: MORankingData[];
}

export const MORanking: React.FC<MORankingProps> = ({ data }) => {
  return (
    <div>
      <h3 style={{ textAlign: 'center', marginBottom: '16px', fontSize: '18px', fontWeight: 600 }}>
        ,  1000 .  .
      </h3>
      <ResponsiveContainer width="100%" height={400}>
        <BarChart
          data={data}
          margin={{ top: 20, right: 60, bottom: 60, left: 20 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis
            dataKey="name"
            angle={-45}
            textAnchor="end"
            height={150}
            tick={{ fontSize: 10 }}
          />
          <YAxis
            yAxisId="left"
            label={{ value: ',  1000 ', angle: -90, position: 'insideLeft' }}
            domain={[0, 500]}
          />
          <YAxis
            yAxisId="right"
            orientation="right"
            domain={[0, 100]}
            label={{ value: ' , %', angle: 90, position: 'insideRight' }}
          />
          <Tooltip
            formatter={(value: number, name: string) => {
              if (name === 'cumulativePercent') {
                return [`${value.toFixed(1)}%`, ' '];
              }
              return [value, name === 'mortalityRate' ? '' : ''];
            }}
          />
          <Legend />
          <Bar yAxisId="left" dataKey="mortalityRate" name="" radius={[4, 4, 0, 0]}>
            {data.map((entry, index) => (
              <Cell
                key={`cell-${index}`}
                fill={entry.isSignificant ? '#f44336' : '#9e9e9e'}
              />
            ))}
          </Bar>
          <Line
            yAxisId="right"
            type="monotone"
            dataKey="cumulativePercent"
            stroke="#1976d2"
            strokeWidth={2}
            name=" "
            dot={false}
          />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

